import { Artifact } from "./Artifact";
import { AssociationDirection } from "./AssociationDirection";
import { BpmnModel } from "./BpmnModel";
import { BpmnModelElement } from "./BpmnModelElement";
import { ExtensionMethod } from "./ExtensionMethod";
import { IClrTypeElement } from "./IClrTypeElement";
import { ValidationError } from "./ValidationError";

export class Association extends Artifact implements IClrTypeElement {

    readonly ClrTypeID: string = "bpmn.Association";

    AssociationDirection: AssociationDirection = AssociationDirection.NONE;
    SourceRef: string;
    TargetRef: string;

    constructor(model: BpmnModel) {
        super(model);
    }

    public ConvertToJsonObject(): any {
        let obj: any = super.ConvertToJsonObject();
        ExtensionMethod.SetAttributeValue(obj, "SourceRef", this.SourceRef);
        ExtensionMethod.SetAttributeValue(obj, "TargetRef", this.TargetRef);
        return obj;
    }

    public LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);
        this.SourceRef = ExtensionMethod.GetAttributeValue4String(obj, "SourceRef");
        this.TargetRef = ExtensionMethod.GetAttributeValue4String(obj, "TargetRef");
    }

    public Validate(errors: Array<ValidationError>): void {
        super.Validate(errors);
    }

    NewInstance(): BpmnModelElement {
        return new Association(this.Model);
    }
}